

























import { Vue, Component, Watch, Prop } from "vue-property-decorator";
import { Mutation } from "vuex-class";

import brace from "brace";
import "brace/theme/tomorrow";
import "@/editor/rdt";

import ISelection from "@/types/selection";

// @ts-ignore
import { Ace as AceEditor } from "vue2-brace-editor";

@Component({
  components: {
    AceEditor,
  },
})
export default class Editor extends Vue {
  @Prop(Number) size!: number;
  @Prop(Number) scroll!: number;
  @Prop(Number) width!: number;
  @Prop(Object) selection!: ISelection | null;

  name = "rdt-editor";

  editor?: any;
  renderer?: any;

  @Mutation("save") save!: (content: string) => void;
  @Mutation("setScroll") setScroll!: (scroll: number) => void;
  @Mutation("setSelection") setSelection!: (
    selection: ISelection | null
  ) => void;

  mounted(): void {
    window.addEventListener("resize", this.handleResize);
  }

  beforeDestroy(): void {
    window.removeEventListener("resize", this.handleResize);
  }

  @Watch("width")
  onWidthChanged(): void {
    this.handleResize();
  }

  @Watch("scroll")
  onScrollChanged(scroll: number): void {
    this.renderer.scrollToY(scroll);
  }

  @Watch("selection")
  onSelectionChanged({ row, offset, length, from }: ISelection): void {
    if (from != "editor") {
      const { selection } = this.editor.getSession();
      selection.setRange({
        start: {
          row: row,
          column: offset,
        },
        end: {
          row: row,
          column: offset + length,
        },
      });
      this.renderer.scrollToX(0);
      this.renderer.scrollSelectionIntoView();
      this.editor.focus();
    }
  }

  onBeforeLoad(): void {
    this.editor = brace.edit(this.name);
    this.renderer = this.editor.renderer;
  }

  onChange(content: string): void {
    this.save(content);
  }

  onScroll(): void {
    this.setScroll(this.renderer.scrollTop);
  }

  onSelectionChange(selection: any): void {
    const { start } = selection.getRange();
    this.setSelection({
      row: start.row,
      offset: start.column,
      length: 0,
      from: "editor",
    });
  }

  handleResize(): void {
    this.editor.resize();
  }

  getCompletions(
    editor: any,
    session: any,
    pos: any,
    prefix: any,
    callback: any
  ): void {
    const icons = this.$store.state.icon.data;
    callback(
      null,
      Object.keys(icons)
        .filter((icon) => !!icons[icon])
        .map((icon) => ({ value: icon }))
    );
  }

  getDocTooltip(item: any): void {
    const icons = this.$store.state.icon.data;
    item.docHTML = `<img src="${icons[item.value]}" class="preview" />`;
  }
}
